.container{
    width: 100%;
    max-width: 1145px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
}
.grid{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin: 0px 0px 70px 0px;
    @media (max-width: $medium) {
        display: block;
    }
    &-2{
        > div {
            width: 46%;
            margin-right: 25px;
            @media (max-width: $medium) {
                width: 100%;
            }
            &:last-child{
                margin-left: 25px;
                @media (max-width: $medium) {
                    margin-left: 0px;
                }
            }
        }
        &-tier{
            > div {
                width: 35%;
                margin-right: 25px;
                @media (max-width: $medium) {
                    width: 100%;
                }
                &:last-child{
                    width: 55%;
                    margin-left: 25px;
                    @media (max-width: $medium) {
                        width: 100%;
                        margin-left: 0px;
                    }
                }
            } 
            &.revert{
                > div {
                    width: 55%;
                    margin-right: 50px;
                    @media (max-width: $medium) {
                        width: 100%;
                    }
                    &:last-child{
                        width: 25%;
                        margin-left: 100px;
                        @media (max-width: $medium) {
                            width: 100%;
                            margin-left: 0px;
                        }
                    }
                } 
            }
        }
    }
    &-3{
        > div{
            width: 30%;
            padding: 15px;
            margin: 0px 10px;
            @media (max-width: $medium) {
                width: 100%;
                padding: 0px 15px;
                margin: 0px;
            }
            &.has-arrow{
                position: relative;
                svg{
                    position: absolute;
                    left: -20px;
                    top: 40px;
                    @media (max-width: $medium) {
                        top: 4px;
                        left: -10px;
                    }
                }
            }
        }
    }
}

.content-2cols{
    margin: 100px 0px;
    @media (max-width: $medium) {
        margin: 20px 0px;
    }
    p{
        margin-left: 36px;
        @media (max-width: $medium) {
            margin-left: 0px;
        }
    }
    .illustration{
        // text-align: center;
    }
}

.reverse{
    .illustration{
        margin-left: 0px !important;
    }
    .description{
        order: 1;
        margin-left: 25px;
        @media (max-width: $medium) {
            margin-left: 0px;
        }
        ul{
            margin-left: 10px;
            margin-top: 30px;
            list-style: none;
            li{
                padding: 5px 0px;
                color: #163253;
                font-family: 'SourceSans';
                &:before{
                    content: "\2022";  /* Add content: \2022 is the CSS Code/unicode for a bullet */
                    color: #E9B2A0; /* Change the color */
                    font-weight: bold; /* If you want it to be bold */
                    display: inline-block; /* Needed to add space between the bullet and the text */
                    width: 1em; /* Also needed for space (tweak if needed) */
                    margin-left: -1em; /* Also needed for space (tweak if needed) */
                }
            }
        }
        @media (max-width: $medium) {
            order: -1;
        }
    }
}