footer{
    position: relative;
    background: url('../img/dateam-contact-v2.svg');
    background-size: cover;
    background-position: -750px 100px;
    background-repeat: no-repeat;
    min-height: 1000px;
    padding-top: 100px;
    margin-top: -500px;
    z-index: 1;
    @media (min-width: 1400px) {
        background-position: -600px 75px;
    }
    @media (max-width: 998px) {
        background-position: -789px 600px;
        padding-bottom: 50px;
    }
    @media (max-width: 448px) {
        margin-top: -800px;
        background-position: -789px 600px;
        padding-top: 0px;
    }
    .grid{
        margin-bottom: 0px;
    }
    .grid-2>div{
        width: 45% ;
        @media (max-width: 768px) {
            width: 100%;
        }
    }
    .icon-social {
        opacity: 1;
        transition: all .3s ease-in-out;
        &:hover{
            opacity: 0.7 !important;
            img{
                transform: scale(1.05) !important;
            }
        }
    }
    .credits{
        position: absolute;
        bottom: 30px;
        left: 70px;
        @media (max-width: 998px) {
            background-color: #253F5E;
            left: 0px;
            right: 0px;
            bottom: 0px;
            padding: 15px;
        }
        .credits-infos{
            font-family: 'SourceSans';
            font-weight: 400;
            font-size: 1.6rem;
            color: #FFFFFF;
        }
        .credits-links{
            color: #A7A8C9;
            a{
                color: #A7A8C9;
                font-family: 'SourceSans';
                font-weight: 400;
                font-size: 1.3rem;
                text-decoration: none;
                margin: 0px 10px;
                &:first-child{
                    margin-left: 0px;
                }
            }
        }
    }

}

.form-content{
    text-align: center;
    margin-top: 25px;
    .parent-captcha{
        text-align: center;
        width: 100%;
        .g-recaptcha{
            div{
                display: block;
            margin: 20px auto;
            }
        }
    }
    .social-list{
        margin-bottom: 20px;
        a{
            text-decoration: none;
            margin: 0px 5px;
            opacity: 1;
            transition: opacity .3s ease-in-out;
            &:hover{
                opacity: 0.7;
            }
        }
    }
    .sending{
        text-align: right;
    }

    form .custom-label{
        position: absolute;
        font-size: 1.45rem;
        left: 15px;
        pointer-events: none;
        color: #A7A8C9;
        transition: all .22s;
        top: 50%;
        transform: translateY(-50%);
        display: block;
      }
      form > div {
          position: relative;
      }
      
      form input, form textarea{
        width: 100%;
        padding: 1.5rem;
        background: #fff;
        margin: 1rem 0;
        border: 2px solid #E6E6E6;
        border-radius: 5px;
        font-size: 1.45rem;
        
        letter-spacing: 0;
        border: 1px solid #A7A8C9;
        &::placeholder{
            color: #A7A8C9;
        }
      }
      
      form input:focus, form textarea:focus{
        background: #fff;
        outline: 0;
      }
      
      form input:focus + .custom-label,
      form input:hover + .custom-label,
      form input:valid + .custom-label,
      form textarea:focus + .custom-label,
      form textarea:hover + .custom-label,
      form textarea:valid + .custom-label
      {
        top: -3px;
        font-size: 1.125rem;
        transform: translateY(7px);
        left: 30;
        opacity: 1;
        background: #fff;
        padding: 0px 5px; 
        color: #163253;
        
      }

      form input:hover, form textarea:hover{
        border: 1px solid #163253;
      }
      form input:hover + .custom-label, form textarea:hover + .custom-label{
          color: #163253;
      }

      .form-group {
        margin-bottom: 1.5em;
    }
    
    .checkbox {
        padding-left: 2em;
        position: relative;
        display: flex;
        justify-content: flex-start;
        background-color: transparent;
        align-items: baseline;
        width: 100%;
        box-shadow: none;
        margin: 30px 0px;
        @media (max-width: 998px) {
            margin-bottom: 30px;
        }
        &-inline {
            display: inline-flex;
        }
        
        input {
            width: 2em;
            flex-basis: 2em;
            margin-left: -2em;
            position: relative;
            vertical-align: baseline;
        }
        
        input:not(.reset) {
            position: absolute;
            left: 0;
            margin-left: 0;
            opacity: 0;
            
            &:focus {
                outline: none;
            }
            
            &:focus ~ label::before {
                border-color: #0877db;
                box-shadow: 0 0 0 0.1875em rgba(43, 154, 255, 0.2);
            }
                    
            &:active ~ label::before {
                border-color: #0877db;
            }
            
            &:checked ~ label::before {
                background-position: 0 0;
            }
        }
        
        input:not(.reset) ~ label::before {
            content: '';
            box-sizing: border-box;
            position: absolute;
            top: 0px;
            left: 0px;
            display: inline-block;
            vertical-align: top;
            width: 1.5em;
            height: 1.5em;
            border: 0.125em solid #163253;
            background-color: #FFF;
            border-radius: 5px;
            background-size: 100% 100%;
            background-position: 0 1.5em;
            background-repeat: no-repeat;
            box-shadow: 0 0 0 0 rgba(43, 154, 255, 0.2);
            transition: box-shadow .15s, background-position .15s;
        }
        
        [type="checkbox"]:not(.reset) ~ label::before {
            background-image: url(data:image/svg+xml;base64,);
        }
        
        [type="radio"]:not(.reset) ~ label::before {
            border-radius: 100%;
            background-image: radial-gradient(circle, #cb0000 0%, #cb0000 40%, #fff 41%, #fff 100%);
            background-position: 50% 50%;
            background-size: 0px 0px;
            transition: box-shadow .15s, background-size .15s;
        }
        [type="radio"]:not(.reset):checked ~ label::before {
            background-position: 50% 50%;
            background-size: 100% 100%;
        }
    
        label {
            display: inline-block;
            vertical-align: baseline;
            user-select: none;
            flex-grow: 1;
            cursor: pointer;
            color: #163253;

            a{
                color: #6978b9;
                font-family: 'SourceSansBold';
            }
        }
        
        &-firstline label::first-line {
            font-weight: bolder;
        }
        
        &:not(.checkbox-firstline) label {
            font-weight: bolder;
        }
        
        
    }
}

.status_form{
    position: fixed;
    display: block;
    bottom: 20px;
    right: 20px;
    background: #163253;
    color: $white;
    padding: 10px 25px;
    &.error{
        background-color: rgb(221, 126, 63);
    }
}