.has-bg-purple{
    position: relative;
    margin-top: 0px;
    background: rgb(23,50,82);
background: -moz-linear-gradient(0deg, rgba(23,50,82,1) 0%, rgba(23,50,82,1) 10%, rgba(66,97,133,1) 36%, rgba(66,97,133,1) 65%, rgba(23,50,82,1) 90%, rgba(23,50,82,1) 100%);
background: -webkit-linear-gradient(0deg, rgba(23,50,82,1) 0%, rgba(23,50,82,1) 10%, rgba(66,97,133,1) 36%, rgba(66,97,133,1) 65%, rgba(23,50,82,1) 90%, rgba(23,50,82,1) 100%);
background: linear-gradient(0deg, rgba(23,50,82,1) 0%, rgba(23,50,82,1) 10%, rgba(66,97,133,1) 36%, rgba(66,97,133,1) 65%, rgba(23,50,82,1) 90%, rgba(23,50,82,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#173252",endColorstr="#173252",GradientType=1);
    padding: 70px 0px;
    @media (max-width: $medium) {
        // overflow-y: hidden;
    }
    &:before{
        position: absolute;
        z-index: 0;
        overflow-x: hidden;
        top: -205px;
        left: 50%;
        transform: translateX(-50%);
        width: 300%;
        height: 300px;
        content: '';
        background: url('../img/shape-top.png');
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
        @media (max-width: $medium) {
            top: -177px;
        }
    }
    &:after{
        position: absolute;
        z-index: 0;
        bottom: -205px;
        overflow-x: hidden;
        left: 50%;
        transform: translateX(-50%);
        width: 300%;
        height: 306px;
        content: '';
        background: url('../img/shape-bottom.png');
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
        @media (max-width: $medium) {
            bottom: -225px;
            left: 0px;
        }
    }
    #bg-header{
        position: absolute;
        top: -139px;
        left: -500px;
        width: 2880px;
        max-width: initial;
        path{
            fill: #163253;
        }
        @media (max-width: $large) {
            position: absolute;
            top: -150px;
            left: -150px;
            width: 150%;
            max-width: initial;
        }
        @media (max-width: 767px) {
            position: absolue;
            top: -171px;
            left: 0px;
            width: 100%;
            max-width: initial;
        }
    }
    #bg-footer{
        position: absolute;
        bottom: -100px;
        left: -600px;
        width: 2880px;
        max-width: initial;
        path{
            fill: #163253;
        }
        @media (max-width: $medium) {
            position: absolute;
            bottom: -165px;
            left: 0px;
            width: 100%;
            max-width: initial;
        }
    }
    .content{
        position: relative;
        .profil{
            margin-bottom: 20px;
        }
        &.small-center{
            text-align: center;
            max-width: 800px;
            margin: 100px auto;
            @media (max-width: $medium) {
                margin: 50px auto;
            }
            h2{
                text-align: center;
            }
        }
        .animation{
            position: absolute;
            left: 0px;
            right: 0px;
            width: 100%;
            .has-bubble-right{
                position: absolute;
                height: 600px;
                width: 600px;
                top:-300px;
                right: -400px;
                
                transform: rotate(-300deg);
                @media (max-width: $medium) {
                    display: none;
                }
                &.middle{
                    top: 50%;
                }
            }
            .has-bubble-left{
                position: absolute;
                height: 600px;
                width: 600px;
                top:50%;
                left: -600px;
                right: 120%;
                transform: rotate(-350deg);
                @media (max-width: $medium) {
                    display: none;
                }
            }
        }
    }
    .has-bubble-center{
        position: relative;
        &:before{
            position: absolute;
            content: '';
            background: url('../img/bg-big-bubble.svg');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center center;
            height: 600px;
            width: 600px;
            top:50%;
            left: 40%;
            transform: translate(-50%,-50%);
        }
    }
    .has-bubble-right{
        position: relative;
        &:before{
            position: absolute;
            content: '';
            background: url('../img/bg-big-bubble.svg');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center center;
            height: 600px;
            width: 600px;
            bottom: -100px;
            right: -500px;
            transform: rotate(-130deg);
            @media (max-width: $medium) {
                display: none;
            }
        }
    }
    .has-bubble-left{
        position: relative;
        &:before{
            position: absolute;
            content: '';
            background: url('../img/bg-big-bubble.svg');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center center;
            height: 600px;
            width: 600px;
            bottom: -100px;
            right: 100%;
            left: -700px;
            transform: rotate(0deg);
            @media (max-width: $medium) {
                display: none;
            }
        }
    }
    h2{
        font-family: 'SourceSansSemiBold';
        margin-top: 15px;
        font-size: 6.4rem;
        line-height: 6.5rem;
        color: $white;
        letter-spacing: 0;
        text-align: left;
        font-weight: 500;
        &.txt-center{
            text-align: center;
        }
        @media (max-width: $medium) {
            font-size: 3rem;
            line-height: 4rem;
        }
    }
    .sub-title{
        font-family: 'SourceSans';
        font-size: 2.5rem;
        color: #A7A8C9;
    }
    p{
        color: $white;
        &.big{
            font-size: 2rem;
            margin-bottom: 0px;
            &.bis{
                font-size: 3rem;
            }
        }
    }
    .relative{
        position: relative;

    }
    .parent-bubble{
        @media (max-width: $medium) {
            margin-top: 150px;
        }
    }
    .bubble-quote{
        &:before{
            position: absolute;
            content: '';
            background: url('../img/bg-bubble-citation.svg');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center center;
            height: 300px;
            width: 360px;
            z-index: 0;
        }
        span{
            position: relative;
            z-index: 5;
        }
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 300px;
        top: 50%;
        font-family: 'Roboto Slab', serif;
        font-size: 2rem;
        color: #E9B2A0;
        text-align: center;
        @media (max-width: 1050px) {
            position: initial;
            width: 100%;
            margin-bottom: 150px;
            margin-top: 150px;
        }
    }
    .double-bubble-quote{
        position: relative;
        margin-bottom: 150px;
        margin-top: 100px;
        &:before{
            position: absolute;
            left: 0px;
            top: -100px;
            content: '';
            background: url('../img/bg-bubble-citation.svg');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center center;
            height: 300px;
            width: 360px;
            z-index: 0;
        }
        &:after{
            position: absolute;
            content: '';
            background: url('../img/bg-bubble-citation.svg');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center center;
            height: 300px;
            width: 360px;
            z-index: 0;
            right: 0px;
            top: -100px;
            @media (max-width: $medium) {
                top: 100px;
            }
        }
        span{
            display: block;
            position: relative;
            z-index: 5;
            margin: 5px 0px;
        }
        font-family: 'Roboto Slab', serif;
        max-width: 600px;
        margin: 0 auto;
        top: 50%;
        font-size: 2rem;
        color: #E9B2A0;
        text-align: center;
        @media (max-width: $medium) {
            position: relative;
            width: 100%;
            margin-bottom: 150px;
        }
    }
    .panel-offre{
        border: 3px solid #E9B2A0;
        border-radius: 12px;
        padding: 32px 24px;
        height: fit-content;
        margin-top: 40px;
        .sub-title{
            display: block;
            margin-top: 15px;
            font-family: 'SourceSans';
            font-size: 1.5rem;
            color: #A7A8C9;
        }
        h3{
            font-family: 'SourceSans';
            font-weight: 400;
            font-size: 2.8rem;
            color: #FFFFFF;
            background: #6978B9;
            text-align: center;
            width: fit-content;
            margin :0 auto ;
            padding: 0px 15px;
        }
        p{
            margin: 10px 0px;
            font-size: 1.5rem;
            &.big{
                font-size: 2.1rem;
            }
        }
    }
}