/* header */
html .load-hidden{
  visibility: hidden;
  
}
html{
  width: 100%;
  overflow-x: hidden;
}
body{
  -webkit-font-smoothing: antialiased;
  @media (max-width: $medium) {
    // overflow-x: hidden;
  }
}
.header {
    position: fixed;
    width: 100%;
    z-index: 100;
    padding: 0px 1%;
    font-family: 'SourceSans';
    transition: background .3s ease-in-out;
    .logo{
      svg{
        path{
          fill: #fff;
        }
      }
    }
    
    &.active{
      background: #fff;
      ul li a{
        color: #163253;
        
        &.active{
          border-bottom: 3px solid #163253;
        }
      }
      .logo{
        svg{
          path{
            fill: #163253;
          }
        }
      }
      .btn{
        color: #fff;
      }
      
    }
    &.innerpage{
      background: #fff;
      ul li a{
        color: #163253;
        &.active{
          border-bottom: 3px solid #163253;
        }
      }
      .logo{
        svg{
          path{
            fill: #163253;
          }
        }
      }
      .btn{
        color: #fff;
      }
    }
    @media (max-width: $large) {
      background: #fff;
      z-index: 100;
      .logo{
        svg{
          path{
            fill: #163253;
          }
        }
      }
      ul li a{
        color: #163253;
      }
    }
  }
  
  .header ul {
    margin: 0;
    padding: 0;
    list-style: none;
    overflow: hidden;
    width: 65%;
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    height: 72px;
    @media (max-width: 1300px) {
      width: 80%;
  }
    @media (max-width: 998px) {
        display: block;
        width: 100%;
        height: 100vh;
    }

    li{
      .btn{
        margin-left: 20px;
        margin-right: 20px;
        @media (max-width: $medium) {
          margin: 20px 0px;
      }
      }
    }
  }
  
  .header li a {
    display: block;
    font-size: 1.3rem;
    padding: 26px 20px;
    text-decoration: none;
    color: $white;
    text-transform: uppercase;
    &.active{
      border-bottom: 2px solid #fff;
    }
    @media (max-width: $large) {
      font-size: 2rem;
      text-transform: initial;

      &.active{
        border-bottom: 2px solid #163253;
      }
    }
  }
  
  .header li a:hover,
  .header .menu-btn:hover {
    // background-color: #f4f4f4;
  }
  
  .header .logo {
    display: flex;
    float: left;
    font-size: 2em;
    padding: 10px 20px;
    text-decoration: none;
    width: 20%;
    @media (max-width: $large) {
      font-size: 2rem;
      width: 35%;
    }
  }
  
  /* menu */
  
  .header .menu {
    clear: both;
    max-height: 0;
    transition: max-height .2s ease-out;
  }
  
  /* menu icon */
  
  .header .menu-icon {
    cursor: pointer;
    display: inline-block;
    float: right;
    padding: 28px 20px;
    position: relative;
    user-select: none;
  }
  
  .header .menu-icon .navicon {
    background: #163253;
    display: block;
    height: 2px;
    position: relative;
    transition: background .2s ease-out;
    width: 18px;
  }
  
  .header .menu-icon .navicon:before,
  .header .menu-icon .navicon:after {
    background: #163253;
    content: '';
    display: block;
    height: 100%;
    position: absolute;
    transition: all .2s ease-out;
    width: 100%;
  }
  
  .header .menu-icon .navicon:before {
    top: 5px;
  }
  
  .header .menu-icon .navicon:after {
    top: -5px;
  }
  
  /* menu btn */
  
  .header .menu-btn {
    display: none;
  }
  
  .header .menu-btn:checked ~ .menu {
    max-height: 240px;
    @media (max-width: $large) {
      max-height: 100%;
  }
  }
  
  .header .menu-btn:checked ~ .menu-icon .navicon {
    background: transparent;
  }
  
  .header .menu-btn:checked ~ .menu-icon .navicon:before {
    transform: rotate(-45deg);
  }
  
  .header .menu-btn:checked ~ .menu-icon .navicon:after {
    transform: rotate(45deg);
  }
  
  .header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
  .header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
    top: 0;
  }
  
  /* 48em = 768px */
  
  @media (min-width: 998px) {
    .header li {
      float: left;
    }
    .header li a {
      padding: 26px 20px;
    }
    .header .menu {
      clear: none;
      float: right;
      max-height: none;
    }
    .header .menu-icon {
      display: none;
    }
  }
  