.front-page{
    footer{
        margin-top: 150px;
    }
    &.contact-page{
        footer{
            min-height: 500px;
            background-position: left top;
        }
    }
}
html{
    &.sr{
        .lvl1, .lvl2, .lvl3, .lvl4, .lvl5, .lvl6, .animmenu{
            visibility: hidden;
        }
    }
}
.header-content{
    background: url('../img/shape-accueil.svg');
    background-size: cover;
    background-position: bottom center;
    background-repeat: no-repeat;
    min-height: 650px;
    padding-top: 130px;
    @media (max-width: $medium) {
        // overflow-x: hidden;
    }
    h1{
        position: relative;
        z-index: 10;
        font-size: 7.5rem;
        line-height: 8.5rem;
        color: $white;
        letter-spacing: 0;
        text-align: left;
        font-weight: 700;
        .highlight{
            position: relative;
            &:before{
            position: absolute;
            content: '';
             width: 110%;
             height: 70px;
             background: $main;
             padding: 0px 5px;   
             z-index: -1;
             top: 24px;
             left: -5px;
            }
        }
    }
    h2{
        font-family: 'SourceSansSemiBold';
        margin-top: 15px;
        font-size: 2.5rem;
        line-height: 3rem;
        color: $white;
        letter-spacing: 0;
        text-align: left;
        font-weight: 400;
        text-transform: uppercase;
    }
    p{
     color: $white;   
    }
    .sub-title{
        font-family: 'SourceSans';
        font-size: 2.5rem;
        color: #A7A8C9;
    }
    .description{
        width: 35%;
        margin-right: 0px;
        @media (max-width: $medium) {
            width: 100%;
        }
    }
    .illustration{
        width: 65%;
        text-align: right;
        margin-left: 0px !important;
        margin-right: 0px;
        @media (max-width: $large) {
            width: 100%;
            position: relative;
            right: -40px;
        }
    }

    &.inner{
        position: relative;
        background: none;
        padding-top: 150px;
        min-height: 750px;
        @media (min-height: 700px) {
            min-height: 850px;
        }
        @media (max-width: $medium) {
            padding-top: 100px;
            // overflow-x: hidden;
            min-height: 800px;
        }
        @media (min-width: 1650px) {
            min-height: 750px;
        }
        &.offres{
            @media (min-height: 800px) {
                min-height: 900px;
            }
            @media (min-width: 1650px) {
                min-height: 900px;
            }
            svg{
                bottom: 50px;
            }
            
        }
        &.qsn{
           
            .illustration{
                &.align-right{
                    svg{
                        bottom: 60px;
                        @media (max-width: 1400px) {
                            bottom: 20px;
                        }
                        @media (max-width: 1200px) {
                            bottom: -30px;
                        }
                        @media (max-width: 1050px) {
                            bottom: 30px;
                        }
                    }
                }
            }
            @media (min-height: 800px)   {
                min-height: 750px;
                .illustration{
                    
                    &.align-right{
                        svg{
                            bottom: 20px;
                        }
                    }
                }
            }
           
            @media (max-width: $medium) {
                min-height: 650px;
                .illustration{
                    right: -200px;
                    &.align-right{
                        svg{
                            bottom: -250px;
                            left: -50px;
                        }
                    }
                }
            }

            @media (max-width: 550px) {
                min-height: 700px;
                .illustration{
                    right: -50px;
                    &.align-right{
                        svg{
                            bottom: -220px;
                            left: -50px;
                        }
                    }
                }
            }
        }
        &.expertises{
            
            @media (max-width: 1500px) {
                min-height: 1100px;
            }
            @media (max-width: 1050px) {
                min-height: 700px;
            }
            @media (max-width: $medium) {
                padding-top: 100px;
                min-height: 700px;
            }
           
            .illustration{
                @media (max-width: 767px) {
                    position: relative;
                    width: 100%;
                    bottom: -275px;
                }
            }
        }
        h1{
            font-family: 'SourceSansSemiBold';
            color: #163253;
            font-size: 5rem;
            line-height: 5rem;
            @media (max-width: $medium) {
                font-size: 4rem;
                line-height: 4rem;
            }
        }
        p{
            color: #163253;
            font-size: 2.1rem;
            @media (max-width: $medium) {
                font-size: 1.7rem;
            }
        }
        .description{
            width: 50%;
            @media (max-width: $medium) {
                width: 100%;
            }
        }
        .illustration{
            width: 50%;
            @media (max-width: $large) {
                position: relative;
                width: 100%;
                bottom: -300px;
            }
            @media (max-width: 767px) {
                position: relative;
                width: 100%;
                bottom: -200px;
            }
            svg{
                position: absolute;
                bottom: 50px;   
                right: 0px;
                
                @media (max-width: $medium) {
                    right: -0px;
                    bottom: -150px;
                    width: 600px;
                    max-width: initial;
                    z-index: -1;
                }
            }
            &.align-right{
                svg{
                    right: -300px;
                    bottom: 20px;
                    @media (max-width: $medium) {
                        right: -0px;
                        bottom: -150px;
                        width: 600px;
                        max-width: initial;
                        z-index: -1;
                    }
                    
                }
            }
            &.align-right-small{
                @media (max-width: $medium) {
                    bottom: -290px;
                }
                svg{
                    right: -100px;
                    bottom: 80px;
                    @media (max-width: $medium) {
                        right: -0px;
                        bottom: -150px;
                        width: 600px;
                        max-width: initial;
                        z-index: -1;
                    }
                }
            }
        }
        
        &.contact{
            h1{
                text-align: center;
                margin-bottom: 30px;
            }
            p{
                font-size: 2.1rem;
                line-height: 3rem;
                small{
                    font-size: 1.7rem;
                    line-height: 2rem;
                }
            }
            .icon-social {
                opacity: 1;
                transition: all .3s ease-in-out;
                &:hover{
                    opacity: 0.7 !important;
                    img{
                        transform: scale(1.05) !important;
                    }
                }
            }
            &.mentions{
                h2{
                    text-align: left;
                    margin-bottom: 30px;
                    color: #163253;
                    font-weight: 700;
                    text-transform: initial;
                    margin-top: 30px;
                    margin-bottom: 10px;
                }
                p{
                    font-size: 2.1rem;
                    line-height: 3rem;
                    small{
                        font-size: 1.7rem;
                        line-height: 2.4rem;
                    }
                }
                ul{
                    li{
                        a{
                            font-size: 2.1rem;
                            line-height: 3rem;
                            color: #163253;
                            text-decoration: none;
                        }
                    }
                }
            }
            .form-content{
                margin-top: 0px;
            }
            .social-list{
                margin-bottom: 20px;
                a{
                    text-decoration: none;
                    margin: 0px 5px;
                }
            }
        }

        &.realisations{
            min-height: 700px;
            @media (max-width: $medium) {
                min-height: 500px;
            }
            
            h1{
                text-align: center;
                margin-bottom: 30px;
                @media (max-width: $medium) {
                    font-size: 3rem;
                    line-height: 3rem;
                }
            }
            .form-content{
                margin-top: 0px;
            }
            svg{
                position: absolute;
                bottom: 50px;   
                left: 50%;
                transform: translateX(-50%);
                @media (min-width: 1600px) {
                    position: absolute;
                    bottom: 50px;   
                    left: 50%;
                    transform: translateX(-50%);
                }
                @media (max-width: 1400px) {
                    position: absolute;
                    bottom: 0px;   
                    left: 50%;
                    transform: translateX(-50%);
                }
                @media (max-width: $large) {
                    left: 0px;
                    bottom: -100px;
                    width: 600px;
                    max-width: initial;
                    transform: none;
                    z-index: -1;
                }
                @media (max-width: $small) {
                    left: 0px;
                    bottom: -125px;
                    width: 600px;
                    max-width: initial;
                    transform: none;
                    z-index: -1;
                }
            }
        }
    }
}

.methodologie{
    h3.title{
        margin-bottom: 30px;
        @media (max-width: $medium) {
            margin-bottom: 60px;
        }
    }
    .desktop{
        display: block;
        @media (max-width: $medium) {
            display: none;
        }
    }
    .responsive{
        display: none;
        @media (max-width: $medium) {
            display: block;
            margin: 0 auto; 
        }
        &.is-blue{
            .item{
                display: block;
                margin: 0 auto 60px auto;
                text-align: center;
                max-width: 300px;
                .item-methodologie-title{
                    display: block;
                    margin: 0 auto;
                    max-width: 192px;
                    text-align: center;
                    padding: 15px 0px;
                    h2{
                        color: $white;
                        font-size: 1.9rem;
                        line-height: 2.3rem;
                        text-align: center;
                        padding: 0px;
                        margin: 0px;
                    }
                    &.illustration{
                        background: url('../img/shape-accueil.svg');
                        position: absolute;
                        content: '';
                        left: 0px;
                        top: 0px;
    
                    }
                    &.first{
                        background-image: linear-gradient(90deg, #A5AED5 0%, #6978B9 100%);
                    }
                    &.second{
                        background-image: linear-gradient(90deg, #163253 0%, #738498 100%);
                    }
                    &.third{
                        background-image: linear-gradient(90deg, #F6E0D9 0%, #E9B2A0 100%);
                    }
                }
                .item-methodologie-description{
                    svg{
                        margin-top: 16px;
                        margin-bottom: 10px;
                    }
                    h3{
                        display: block;
                        margin: 0 auto;
                        font-size: 2.1rem;
                        line-height: 2.7rem;
                        max-width: 300px;
                        color: $white;
                    }
                }
            }
        }
        .item{
            display: block;
            margin: 0 auto 60px auto;
            text-align: center;
            max-width: 300px;
            .item-methodologie-title{
                display: block;
                margin: 0 auto;
                max-width: 192px;
                text-align: center;
                padding: 15px 0px;
                h2{
                    color: $white;
                    font-size: 1.9rem;
                    line-height: 2.3rem;
                    margin: 0px;
                }
                &.illustration{
                    background: url('../img/shape-accueil.svg');
                    position: absolute;
                    content: '';
                    left: 0px;
                    top: 0px;

                }
                &.first{
                    background-image: linear-gradient(90deg, #A5AED5 0%, #6978B9 100%);
                }
                &.second{
                    background-image: linear-gradient(90deg, #163253 0%, #738498 100%);
                }
                &.third{
                    background-image: linear-gradient(90deg, #F6E0D9 0%, #E9B2A0 100%);
                }
            }
            .item-methodologie-description{
                svg{
                    margin-top: 16px;
                    margin-bottom: 10px;
                }
                h3{
                    display: block;
                    margin: 0 auto;
                    font-size: 2.1rem;
                    line-height: 2.7rem;
                    max-width: 300px;
                }
            }
        }
    }
}


.sunset{
    position: relative;
    animation:ease-in-out;
    animation-name: sunset;
    animation-duration: 3s;
}
.woodleftmoov{
    position: relative;
    animation:ease-in-out;
    animation-name: woodleftmoov;
    animation-duration: 3s;   
}
.woodrightmoov{
    position: relative;
    animation:ease-in-out;
    animation-name: woodrightmoov;
    animation-duration: 3s; 
}
.woodrightmoovbis{
    position: relative;
    animation:ease-in-out;
    animation-name: woodrightmoovbis;
    animation-duration: 4s; 
}
.eyeleft{
    position: relative;
    animation:ease-in-out infinite;
    animation-name: eyeleft;
    animation-duration: 3s; 
}
.eyeright{
    position: relative;
    animation:ease-in-out infinite;
    animation-name: eyeright;
    animation-duration: 3s; 
}
.eyeleftbis{
    position: relative;
    animation:ease-in-out infinite;
    animation-name: eyeleftbis;
    animation-duration: 3s; 
    animation-delay: 2s;
}
.eyerightbis{
    position: relative;
    animation:ease-in-out infinite;
    animation-name: eyerightbis;
    animation-duration: 3s; 
    animation-delay: 2s;
}
.snowlight1{
    position: relative;
    animation:ease-in-out infinite;
    animation-name: snowlight1;
    animation-duration: 2s; 
}
.snowlight2{
    position: relative;
    animation:ease-in-out infinite;
    animation-name: snowlight2;
    animation-duration: 2s; 
}
.snowlight3{
    position: relative;
    animation:ease-in-out infinite;
    animation-name: snowlight3;
    animation-duration: 2s; 
}
.birdflight1{
    position: relative;
    animation:ease-in-out ;
    animation-name: birdflight1;
    animation-duration: 3s; 
}
.birdflight2{
    position: relative;
    animation:ease-in-out;
    animation-name: birdflight2;
    animation-duration: 4s; 
}

.flag{
    position: relative;
    animation:ease-in-out infinite;
    animation-name: flag;
    animation-duration: 1.5s; 
}
@keyframes sunset {
    0% { transform: translate(0px, 135px);}
    100%{ transform: translate(0px, 0px);}
}
@keyframes woodleftmoov {
    0% { transform: translate(-100px, 0px);}
    100%{ transform: translate(0px, 0px);}
}
@keyframes woodrightmoov {
    0% { transform: translate(100px, 0px);}
    100%{ transform: translate(0px, 0px);}
}
@keyframes woodrightmoovbis {
    0% { transform: translate(100px, 0px);}
    100%{ transform: translate(0px, 0px);}
}
@keyframes eyeleft {
    0% { opacity: 0;}
    5% { opacity: 1;}
    49% { opacity: 1;}
    50% { opacity: 1;}
    95%{opacity: 1;}
    100%{ opacity: 0;}
}
@keyframes eyeright {
    0% { opacity: 0;}
    5% { opacity: 1;}
    49% { opacity: 1;}
    50% { opacity: 1;}
    95%{opacity: 1;}
    100%{ opacity: 0;}
}
@keyframes eyeleftbis {
    0% { opacity: 0;}
    5% { opacity: 1;}
    49% { opacity: 1;}
    50% { opacity: 1;}
    95%{opacity: 1;}
    100%{ opacity: 0;}
}
@keyframes eyerightbis {
    0% { opacity: 0;}
    5% { opacity: 1;}
    49% { opacity: 1;}
    50% { opacity: 1;}
    95%{opacity: 1;}
    100%{ opacity: 0;}
}
@keyframes snowlight1 {
    0% { opacity: 1;}
    49% { opacity: 0;}
    100%{ opacity: 1}
}
@keyframes snowlight2 {
    0% { opacity: 1;}
    49% { opacity: 0;}
    100%{ opacity: 1}
}
@keyframes snowlight3 {
    0% { opacity: 0;}
    49% { opacity: 1;}
    100%{ opacity: 0;}
}
@keyframes birdflight1 {
    0% { transform: translate(-25px, 13px);}
    50%{ transform: translate(0px, 0px);}
}
@keyframes birdflight2 {
    0% { transform: translate(-28px, 18px);}
    100%{ transform: translate(0px, 0px);}
}
@keyframes flag {
    0% { opacity: 0;}
    49% { opacity: 1;}
    100%{ opacity: 0;}
}
.animatelateral{
    position: relative;
    animation:ease-in-out infinite alternate;
    animation-name: run2;
    animation-duration: 6s;
}
.cloudmoov{
    position: relative;
    animation:ease-in-out alternate;
    animation-name: run2;
    animation-duration: 5s;
}
.starslight{
    position: relative;
    animation:ease-in-out infinite alternate;
    animation-name: starslight;
    animation-duration: 5s;
}
.floating{
    position: relative;
    animation:ease-in-out infinite alternate;
    animation-name: floating;
    animation-duration: 5s;
}

#Group-39{
    position: relative;
    animation:ease-in-out alternate;
    animation-name: Group39;
    animation-duration: 5s;
}
