@keyframes Group39 {
    0% { transform: translate(50px, 1px);}
    25%{ transform: translate(50px, 30px);}
    50%{ transform: translate(50px, 5px);}
    100%{ transform: translate(50px, 1px);}
}
@keyframes run {
    0% { transform: translate(-55px, 0px);}
    50%{ transform: translate(0px, 0px);}
    100%{ transform: translate(-35px, 0px);}
}
@keyframes run2 {
    0% { transform: translate(-35px, 0px);}
    100%{ transform: translate(0px, 0px);}
}
@keyframes floating {
    0% { transform: translate(-25px, -15px);}
    25% { transform: translate(-25px, -25px);}
    50%{ transform: translate(5px, -15px);}
    100%{ transform: translate(-25px, -15px);}
}
@keyframes starslight {
    0% { transform: opacity(0);}
    50%{ transform: opacity(1);}
    100%{ transform: opacity(0);}
}
