.stars-animations{
    @media (max-width: $medium) {
        display: none;
    }
    &.pos1{
        svg, img{
            position: absolute;
            top: 100px;
            left: 0px;
        }
    }
    &.pos2{
        svg, img{
            position: absolute;
            bottom: -50px;
            left: 200px;
        }
    }
    &.pos3{
        svg, img{
            position: absolute;
            bottom: -50px;
            left: 0px;
        }
    }
    &.pos4{
        svg, img{
            position: absolute;
            bottom: -100px;
            left: -100px;
        }
    }
    &.pos5{
        svg, img{
            position: absolute;
            top: 150px;
            right: 300px;
        }
    }
    &.pos6{
        svg, img{
            position: absolute;
            bottom: -100px;
            right: 0px;
        }
    }
    &.pos7{
        svg, img{
            position: absolute;
            top: 0px;
            left: 0px;
        }
    }
    &.pos8{
        svg, img{
            position: absolute;
            top: 0px;
            left: -200px;
        }
    }
    &.pos9{
        svg, img{
            position: absolute;
            top: 0px;
            right: -200px;
        }
    }
}

.birds-animations{
    &.pos1{
        position: absolute;
        top: 0px;
        left: 0px;
    }
    &.pos2{
        position: absolute;
        top: 0px;
        right: 0px;
    }
}