/*
 * Type / Typography
 * Override some styles over KNACSS for links, headings and other typographic Stuff
 *
 * @author: Alsacreations
 */

// WYSIWYG / user content
.content {
  // Links with surrounding text should be underlined (or else have another effect like bold, italic but we don't want that)
  a {
    text-decoration: underline;
  }
  
}

.txt-center{
  text-align: center;
}

h3{
  font-size: 4.8rem;
  color: #163253;
  font-family: 'SourceSansSemiBold';
  @media (max-width: $medium) {
    font-size: 4rem;
    line-height: 4rem
  }
}

h4{
  font-family: 'SourceSansSemiBold';
  font-size: 2.1rem;
  color: #163253;
}

p{
  font-family: 'SourceSans';
  font-weight: 400;
  font-size: 1.6rem;
  color: #163253;
  margin: 20px 0px ;
  strong{
    font-family: 'SourceSansBold';
  }
  a{
    color: #a0c2ff;
    &:hover{
      color: #fff;
    }
  }
}