/*
 * Buttons
 * Styles for buttons
 *
 * @author: Alsacreations
 */

// KNACSS v7 defines 7 buttons .btn--primary and alike (also .button--7variants). Mapping them to .btn-primary, .btn-secondary, etc
.btn {
  font-family: 'SourceSansSemiBold';
  display: inline-flex !important;
  align-items: center;
  justify-content: center;
  background-color: $main;
  color: $white;
  height: 50px;
  padding: 1rem 2.5rem;
  border-radius: 23px;
  text-transform: uppercase;
  text-decoration: none;
  @media (max-width: $medium) {
    padding: 0.8rem 1.4rem;
}
  svg{
    margin-left: 15px;
  }
  &:hover{
    text-decoration: none;
    background-color: #A0C2FF;
    color: $white;
  }
  &.bold{
    font-weight: 700;
    text-decoration: none;
    box-shadow: none;
    &.twitter{
      background-color: #00ACEE;
    }
  }
  &.full-width{
    text-decoration: none;
    font-family: 'SourceSansBold';
    display: flex;
    margin-left: 36px;
    justify-content: space-between;
    width: 100%;
    margin: 7px 0px;
    background: $white;
    color: $main;
    border: 3px solid $main;
    @media (max-width: $medium) {
        margin-left: 0px;
        padding: 0.8rem 1.4rem;
    }
    &:hover{
      svg{
        .hover{
          fill: $white;
        }
      }
    }
    &.without-border{
      border: none;
      font-family: 'SourceSansSemiBold';
      justify-content: initial;
      text-transform: initial;
      white-space: normal;
      text-align: left;
      margin-left: 0px;
      svg{
        margin-right: 15px;
        width: 50px;
        @media (max-width: $medium) {
            width: 50px;
            margin-left: 0px;
        }
      }
      &:hover{
        background: none;
        color: #A0C2FF;
        border: none;
        svg{
          .hover{
            fill: #A0C2FF;
          }
        }
      }
    }
    &:hover{
      background: #A0C2FF;
      color: $white;
      border: 2px solid #A0C2FF;
    }
  }
}
