/* ----------------------------- */
/* ==Tags                      */
/* ----------------------------- */
/* use .tag-- classes for variants */
.tag,
.tag--primary,
.tag--success,
.tag--info,
.tag--warning,
.tag--danger,
.tag--inverse,
.tag--ghost {
  line-height: 1;
  display: inline-block;
  padding: 3px .5rem;
  vertical-align: baseline;
  white-space: nowrap;
  color: #212529;
  border-radius: 0;
  background-color: #e7e9ed;
}

.tag--primary {
  color: #fff;
  background-color: #0275d8;
  box-shadow: none;
}

.tag--success {
  color: #fff;
  background-color: #0c8a0c;
  box-shadow: none;
}

.tag--info {
  color: #000;
  background-color: #5bc0de;
  box-shadow: none;
}

.tag--warning {
  color: #000;
  background-color: #f0ad4e;
  box-shadow: none;
}

.tag--danger {
  color: #fff;
  background-color: #cd4a47;
  box-shadow: none;
}

.tag--inverse {
  color: #fff;
  background-color: #333;
  box-shadow: none;
}

.tag--ghost {
  color: #fff;
  background-color: transparent;
  box-shadow: 0 0 0 1px #fff inset;
}

.tag--small {
  font-size: 1.2rem;
}

.tag--big {
  font-size: 2rem;
}

.tag--block {
  display: block;
  width: 100% !important;
}

.tag.disabled,
.disabled.tag--primary,
.disabled.tag--success,
.disabled.tag--info,
.disabled.tag--warning,
.disabled.tag--danger,
.disabled.tag--inverse,
.disabled.tag--ghost,
.tag--disabled {
  cursor: not-allowed;
  opacity: .5;
}

.tag:empty,
.tag--primary:empty,
.tag--success:empty,
.tag--info:empty,
.tag--warning:empty,
.tag--danger:empty,
.tag--inverse:empty,
.tag--ghost:empty {
  display: none;
}

/* ----------------------------- */
/* ==Badges                      */
/* ----------------------------- */
/* use .badge-- classes for variants */
.badge,
.badge--primary,
.badge--success,
.badge--info,
