@media (max-width: 1300px) {
  .header ul {
    width: 80%;
  }
}

@media (max-width: 998px) {
  .header ul {
    display: block;
    width: 100%;
    height: 100vh;
  }
}

.header ul li .btn {
  margin-right: 20px;
  margin-left: 20px;
}

@media (max-width: 768px) {
  .header ul li .btn {
    margin: 20px 0;
  }
}

.header li a {
  font-size: 1.3rem;
  display: block;
  padding: 26px 20px;
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
}

.header li a.active {
  border-bottom: 2px solid #fff;
}

@media (max-width: 998px) {
  .header li a {
    font-size: 2rem;
    text-transform: initial;
  }

  .header li a.active {
    border-bottom: 2px solid #163253;
  }
}

