  font-size: 2em;
  display: flex;
  float: left;
  width: 20%;
  padding: 10px 20px;
  text-decoration: none;
}

@media (max-width: 998px) {
  .header .logo {
    font-size: 2rem;
    width: 35%;
  }
}

/* menu */
.header .menu {
  clear: both;
  max-height: 0;
  transition: max-height .2s ease-out;
}

/* menu icon */
.header .menu-icon {
  position: relative;
  display: inline-block;
  float: right;
  padding: 28px 20px;
  cursor: pointer;
  user-select: none;
}

.header .menu-icon .navicon {
  position: relative;
  display: block;
  width: 18px;
  height: 2px;
  transition: background .2s ease-out;
  background: #163253;
}

.header .menu-icon .navicon:before,
.header .menu-icon .navicon:after {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  content: '';
  transition: all .2s ease-out;
  background: #163253;
}

.header .menu-icon .navicon:before {
  top: 5px;
}

.header .menu-icon .navicon:after {
  top: -5px;
}

/* menu btn */
.header .menu-btn {
  display: none;
}

.header .menu-btn:checked ~ .menu {
  max-height: 240px;
}

@media (max-width: 998px) {
  .header .menu-btn:checked ~ .menu {
    max-height: 100%;
  }
}

.header .menu-btn:checked ~ .menu-icon .navicon {
  background: transparent;
}

.header .menu-btn:checked ~ .menu-icon .navicon:before {
  transform: rotate(-45deg);
}

.header .menu-btn:checked ~ .menu-icon .navicon:after {
  transform: rotate(45deg);
}

.header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
.header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
  top: 0;
}

/* 48em = 768px */
@media (min-width: 998px) {
  .header li {
    float: left;
  }

  .header li a {
    padding: 26px 20px;
  }

  .header .menu {
    float: right;
    clear: none;
    max-height: none;
  }

  .header .menu-icon {
    display: none;
  }
}

