.form-content .checkbox {
  position: relative;
  display: flex;
  width: 100%;
  margin: 30px 0;
  padding-left: 2em;
  background-color: transparent;
  box-shadow: none;
  justify-content: flex-start;
  align-items: baseline;
}

@media (max-width: 998px) {
  .form-content .checkbox {
    margin-bottom: 30px;
  }
}

.form-content .checkbox-inline {
  display: inline-flex;
}

.form-content .checkbox input {
  position: relative;
  width: 2em;
  margin-left: -2em;
  vertical-align: baseline;
  flex-basis: 2em;
}

.form-content .checkbox input:not(.reset) {
  position: absolute;
  left: 0;
  margin-left: 0;
  opacity: 0;
}

.form-content .checkbox input:not(.reset):focus {
  outline: none;
}

.form-content .checkbox input:not(.reset):focus ~ label::before {
  border-color: #0877db;
  box-shadow: 0 0 0 .1875em rgba(43, 154, 255, .2);
}

.form-content .checkbox input:not(.reset):active ~ label::before {
  border-color: #0877db;
}

.form-content .checkbox input:not(.reset):checked ~ label::before {
  background-position: 0 0;
}

.form-content .checkbox input:not(.reset) ~ label::before {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  box-sizing: border-box;
  width: 1.5em;
  height: 1.5em;
  content: '';
  transition: box-shadow .15s, background-position .15s;
  vertical-align: top;
  border: .125em solid #163253;
  border-radius: 5px;
  background-color: #fff;
  background-repeat: no-repeat;
  background-position: 0 1.5em;
  background-size: 100% 100%;
  box-shadow: 0 0 0 0 rgba(43, 154, 255, .2);
}

.form-content .checkbox [type='checkbox']:not(.reset) ~ label::before {
  background-image: url(data:image/svg+xml;base64,);
}

.form-content .checkbox [type='radio']:not(.reset) ~ label::before {
  transition: box-shadow .15s, background-size .15s;
  border-radius: 100%;
  background-image: radial-gradient(circle, #cb0000 0%, #cb0000 40%, #fff 41%, #fff 100%);
  background-position: 50% 50%;
  background-size: 0 0;
}

.form-content .checkbox [type='radio']:not(.reset):checked ~ label::before {
  background-position: 50% 50%;
  background-size: 100% 100%;
}

.form-content .checkbox label {
  display: inline-block;
  cursor: pointer;
  user-select: none;
  vertical-align: baseline;
  color: #163253;
  flex-grow: 1;
}

.form-content .checkbox label a {
  font-family: 'SourceSansBold';
  color: #6978b9;
}

.form-content .checkbox-firstline label::first-line {
  font-weight: bolder;
}

.form-content .checkbox:not(.checkbox-firstline) label {
  font-weight: bolder;
}

